@media (max-width: 1000px) {
    
    /* //////////////////////// BUTTON ///////////////////////////// */

    .BurgerMenu-button {
        padding: 10px;
        margin: 0;
        margin-left: 2rem;
        border: none;
        background-color: transparent;
        position: relative;
        /* width: 5rem; */
        height: 50px;

        display: flex;
        align-items: center;
        justify-content: center;

        -webkit-transition: -webkit-transform .3s ease-in-out;

        transition: -webkit-transform .3s ease-in-out;

        transition: transform .3s ease-in-out;

        transition: transform .3s ease-in-out, -webkit-transform .3s ease-in-out;
    }

    .BurgerMenu-button:hover {
        cursor: pointer;
    }

    .BurgerMenu-button--active {
        -webkit-transform: translateX(40rem);
                transform: translateX(40rem);
    }

    .BurgerMenu-span {
        width: 40px;
        height: 4px;
        background-color: white;

        -webkit-transition: background-color .1s ease-in-out;

        transition: background-color .1s ease-in-out;
    }

    .BurgerMenu-span::after,
    .BurgerMenu-span::before {
        content: '';
        width: 40px;
        height: 4px;
        background-color: white;
        position: absolute;
        left: 10px;

        -webkit-transition: -webkit-transform .3s ease-in-out;

        transition: -webkit-transform .3s ease-in-out;

        transition: transform .3s ease-in-out;

        transition: transform .3s ease-in-out, -webkit-transform .3s ease-in-out;
    }

    .BurgerMenu-span::after {
        top: 10px;
    }

    .BurgerMenu-span::before {
        bottom: 10px;
    }

    .BurgerMenu-button--active > .BurgerMenu-span {
        background-color: transparent;
    }

    .BurgerMenu-button--active > .BurgerMenu-span::before {
        -webkit-transform: translateY(-13px) rotate(45deg);
                transform: translateY(-13px) rotate(45deg);
    }

    .BurgerMenu-button--active > .BurgerMenu-span::after {
        -webkit-transform: translateY(13px) rotate(-45deg);
                transform: translateY(13px) rotate(-45deg);
    }

    /* //////////////////////// MENU ///////////////////////////// */

    .BurgerMenu-listDiv {
        height: 100%;
        width: 40rem;
        background-color: black;
        position: absolute;
        top: 0;
        left: 0;
        -webkit-transform: translateX(-40rem);
                transform: translateX(-40rem);
        -webkit-transition: -webkit-transform .3s ease-in-out;
        transition: -webkit-transform .3s ease-in-out;
        transition: transform .3s ease-in-out;
        transition: transform .3s ease-in-out, -webkit-transform .3s ease-in-out;

        z-index: 10;
    }

    .BurgerMenu-listDiv--active {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }

    .BurgerMenu-list {
        list-style: none;
        height: 100%;
        margin-top: 10rem;
        padding: 1rem;

        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;

        position: relative;
    }

    .BurgerMenu-item {
        margin-bottom: 1rem;
    }

    .BurgerMenu-link {
        color: white;
        text-decoration: none;
        display: block;
        padding: 1rem;
        font-size: 2rem;
        text-align: center;
        width: 20rem;
        -webkit-transition: background-color .2s ease-in-out;
        transition: background-color .2s ease-in-out;
    }

    .BurgerMenu-link:visited,
    .BurgerMenu-link:active {
        color: white;
    }

    .BurgerMenu-link:hover {
        cursor: pointer;
        background-color: rgb(44, 44, 44);
    }

    .BurgerMenu-listButton {
        display: none;
    }
}

