    .BurgerMenu-listDiv {
        width: 100%;
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }

    .BurgerMenu-listDiv--active {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }

    .BurgerMenu-item {
        margin-bottom: 4rem;
    }
    
    .BurgerMenu-link {
        font-size: 2.7rem;
        width: 26rem;
        padding: 2rem;
    }
    
    .BurgerMenu-listButton {
        position: absolute;
        border: none;
        background-color: transparent;
        height: 5rem;
        top: 1rem;
        right: 1rem;
        padding: 1rem;
        margin: 0;
        opacity: 0;
    
    
        display: flex;
        align-items: center;
        justify-content: center;
    
        -webkit-transition: opacity .5s ease-in-out;
    
        transition: opacity .5s ease-in-out;
    }
    
    .BurgerMenu-listDiv--active > .BurgerMenu-listButton {
        opacity: 1;
    }
    
    .BurgerMenu-listButton {
        position: absolute;
        top: 1rem;
        right: 1rem;
        padding: 1rem;
        margin: 0;
    }
    
    .BurgerMenu-listButton:hover {
        cursor: pointer;
    }
    
    .BurgerMenu-listSpan {
        width: 4rem;
        height: .4rem;
        background-color: transparent;
    }
    
    .BurgerMenu-listSpan::before,
    .BurgerMenu-listSpan::after {
        content: '';
        width: 4rem;
        height: .4rem;
        background-color: white;
        position: absolute;
        left: 1rem;
    }
    
    .BurgerMenu-listSpan::before {
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
    }
    
    .BurgerMenu-listSpan::after {
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
    }
}

