.DayCard-container {
    padding: 2.5rem 1.5rem;
    height: 40rem;
    width: 30rem;
    border: 1px solid black;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.DayCard-container--partly {
    background-color: rgb(253, 253, 131);
}

